import React, { Component } from "react";
import {BrowserRouter as Router, useLocation, Route, Switch, Link,  } from "react-router-dom";
import TableList from "views/TableList.js";
import AdminNavbar from "components/Navbars/AdminNavbar";
import Footer from "components/Footer/Footer";
import Sidebar from "components/Sidebar/Sidebar";

import routes from "routes.js";

import sidebarImage from "assets/img/loglclr.png";


function Admin() {
  const [image] = React.useState(sidebarImage);
  const [color] = React.useState("azure");
  const [hasImage] = React.useState(true);
  const location = useLocation();
  const mainPanel = React.useRef(null);



  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/u") {
        return (
          <Route
            path={prop.layout + prop.path}
            render={(props) => <prop.component {...props} />}
            key={key} isAuth={prop.isAuth}
          />
        );
      } else {
        return null;
      }
    });
  };

  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    mainPanel.current.scrollTop = 0;
    if (
      window.innerWidth < 993 &&
      document.documentElement.className.indexOf("nav-open") !== -1
    ) {
      document.documentElement.classList.toggle("nav-open");
      var element = document.getElementById("bodyClick");
      element.parentNode.removeChild(element);
    }
  }, [location]);
  return (
    <>



      <div className="wrapper">
        <Sidebar color={color} image={hasImage ? image : ""} routes={routes} />
        <div className="main-panel" ref={mainPanel}>
          <AdminNavbar />

          <div className="content">

            <Switch>{getRoutes(routes)}</Switch>
          </div>
          <Footer />
        </div>
      </div>

    </>
  );
}

export default Admin;
